<template>
	<div>
		<el-row :gutter="0" style="background-color:rgb(238, 238, 238);padding-top: 50px;padding-bottom: 50px;">
			<div class="container">
				<el-col :lg="24" :md="24" :sm="24" :xs="24" style="background-color:#fff;padding:30px 25px 15px 0">
					<p class="tc b f20"></p>
				</el-col>
				<el-col :lg="24" :md="24" :sm="24" :xs="24" style="background-color:#fff;padding:0px 25px 15px 0">
					<el-form :model="applyForm" :rules="applyRules" ref="applyForm" label-width="140px" class="enter-ruleForm">
						<el-form-item label="" prop="founder">
							<el-input type="text" v-model="applyForm.founder"></el-input>
						</el-form-item>
						<el-form-item label="" prop="birth">
							<el-date-picker v-model="applyForm.birth" type="date" placeholder="" @change="timeChage(applyForm.birth)">
							</el-date-picker>
						</el-form-item>
						<el-form-item label="" prop="edu">
							<el-select v-model="applyForm.edu">
								<el-option :label="item['value']" :value="item['index']" v-for="(item, index) in edus" :key="index"></el-option>
							</el-select>
						</el-form-item>
						<el-form-item label="" prop="graduate">
							<el-input type="text" v-model="applyForm.graduate"></el-input>
						</el-form-item>
						<el-form-item label="" prop="specialty">
							<el-input type="text" v-model="applyForm.specialty"></el-input>
						</el-form-item>
						<el-form-item label="" prop="founderIntro">
							<el-input type="textarea" v-model="applyForm.founderIntro"></el-input>
						</el-form-item>
						<el-form-item label="" prop="businessId">
							<el-select v-model="applyForm.businessId">
								<el-option :label="item['value']" :value="item['id']" v-for="(item, index) in category" :key="index"></el-option>
							</el-select>
						</el-form-item>
						<el-form-item label="" prop="name">
							<el-input type="text" v-model="applyForm.name"></el-input>
						</el-form-item>
						<el-form-item label="" prop="status">
							<el-radio-group v-model="applyForm.status">
								<el-radio label="1" :key='1'></el-radio>
								<el-radio label="2" :key='2'></el-radio>
								<el-radio label="3" :key='3'>Pre-AA</el-radio>
								<el-radio label="4" :key='4'>B</el-radio>
							</el-radio-group>
						</el-form-item>
						<el-form-item label="" prop="archived">
							<el-input type="number" v-model="applyForm.archived"></el-input>
						</el-form-item>
						<el-form-item label="" prop="financing">
							<el-input type="number" v-model="applyForm.financing"></el-input>
						</el-form-item>
						<el-form-item label="" prop="unit">
							<el-radio-group v-model="applyForm.unit">
								<el-radio label="" :key='1'></el-radio>
								<el-radio label="" :key='2'></el-radio>
								<el-radio label="" :key='3'></el-radio>
								<el-radio label="" :key='4'></el-radio>
							</el-radio-group>
						</el-form-item>
						<el-form-item label="" prop="logo">
							<!-- <el-upload class="upload-demo" :headers="headers" action="http://www.sanxiachuanggu.com/servant/file" :on-success="handleAvatarSuccess" :on-preview="handlePreview" :on-remove="handleRemove" :file-list="fileList2" list-type="picture">
										<el-button size="small" type="primary"></el-button>
										<div slot="tip" class="el-upload__tip">jpg/png500kb</div>
									</el-upload> -->
							<el-upload :headers="headers" class="avatar-uploader" action="http://www.sanxiachuanggu.com/servant/file" :show-file-list="false" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
								<img v-if="imageUrl" :src="imageUrl" class="avatar">
								<i v-else class="el-icon-plus avatar-uploader-icon"></i>
							</el-upload>
						</el-form-item>
						<el-form-item label="" prop="intro">
							<el-input type="textarea" v-model="applyForm.intro"></el-input>
						</el-form-item>
						<el-form-item label="" prop="scale">
							<el-input type="number" v-model="applyForm.scale"></el-input>
						</el-form-item>
						<el-form-item label="" prop="progress">
							<el-input type="textarea" v-model="applyForm.progress"></el-input>
						</el-form-item>
						<el-form-item label="" prop="what">
							<el-input type="textarea" v-model="applyForm.what"></el-input>
						</el-form-item>
						<el-form-item label="" prop="how">
							<el-input type="textarea" v-model="applyForm.how"></el-input>
						</el-form-item>
						<el-form-item label="" prop="why">
							<el-input type="textarea" v-model="applyForm.why"></el-input>
						</el-form-item>
						<el-form-item label="(%)" prop="ratio">
							<el-input type="number" v-model="applyForm.ratio"></el-input>
						</el-form-item>
						<el-form-item>
							<el-button @click="submitForm('applyForm')" style="background-color: #f48100;border:none;color:#fff;"></el-button>
							<el-button @click="resetForm('applyForm')"></el-button>
						</el-form-item>
					</el-form>
				</el-col>
			</div>
		</el-row>
	</div>
</template>
<style scoped>
	.enter-ruleForm {
		width: 520px;
		margin: 0 auto;
	}
</style>
<script>
	import api from '../axios/api.js'
	export default {
		data() {
			let validateContact = (rule, value, callback) => {
				let re = /^[\u4E00-\u9FA5\uf900-\ufa2d]{2,5}$/;
				if (value === "" || !re.test(value) || value.length < 2 || value.length > 5) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			return {
				fileList2: [],
				category: "",
				logo: "",
				logoId: "",
				birth: "",
				imageUrl: "",
				edus: [{
						'index': '',
						'value': ''
					},
					{
						'index': '',
						'value': ''
					},
					{
						'index': '',
						'value': ''
					},
					{
						'index': '',
						'value': ''
					},
					{
						'index': '',
						'value': ''
					},
					{
						'index': '',
						'value': ''
					},
					{
						'index': '',
						'value': ''
					},
					{
						'index': '',
						'value': ''
					},
					{
						'index': '',
						'value': ''
					},
					{
						'index': '',
						'value': ''
					},
				],
				applyForm: {
					"founder": "", //
					"founderIntro": "", //
					"birth": '', //
					"businessId": "", //
					"status": "", //
					"financing": "", //
					"archived": "", //
					"unit": "", //
					"name": "", //
					"intro": "", //
					"scale": "", //
					"progress": "", //
					"what": "", //
					"how": "", //
					"why": "", //
					"ratio": "", //
					"edu": "", //
					"graduate": "", //
					"specialty": "", //
				},
				applyRules: {
					founder: [{
						required: true,
						message: '',
						trigger: 'blur'
					},{ min: 1, max: 20, message: ' 20 ', trigger: 'blur' }],
					founderIntro: [{
						required: true,
						message: '',
						trigger: 'blur'
					},{ min: 1, max: 250, message: ' 250 ', trigger: 'blur' }],
					birth: [{
						type: 'date',
						required: true,
						message: '',
						trigger: 'change'
					}],
					name: [{
						required: true,
						message: '',
						trigger: 'blur'
					},{ min: 1, max: 20, message: ' 20 ', trigger: 'blur' }],
					businessId: [{
						type: 'number',
						required: true,
						message: '',
						trigger: 'change'
					}],
					status: [{
						required: true,
						message: '',
						trigger: 'change'
					}],
					financing: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					archived: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					unit: [{
						required: true,
						message: '',
						trigger: 'change'
					}],
					intro: [{
						required: true,
						message: '',
						trigger: 'blur'
					},{ min: 1, max: 140, message: ' 140 ', trigger: 'blur' }],
					scale: [{
						required: true,
						message: '',
						trigger: 'blur'
					},{ min: 1, max: 45, message: ' 45 ', trigger: 'blur' }],
					progress: [{
						required: true,
						message: '',
						trigger: 'blur'
					},{ min: 1, max: 45, message: ' 45 ', trigger: 'blur' }],
					what: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					how: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					why: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					radio: [{
						required: true,
						message: '',
						trigger: 'blur'
					},{ min: 1, max: 3, message: ' 3 ', trigger: 'blur' }],
					edu: [{
						required: true,
						message: '',
						trigger: 'change'
					}],
					graduate: [{
						required: true,
						message: '',
						trigger: 'blur'
					},{ min: 1, max: 25, message: ' 25 ', trigger: 'blur' }],
					specialty: [{
						required: true,
						message: '',
						trigger: 'blur'
					},{ min: 1, max: 25, message: ' 25 ', trigger: 'blur' }],
				}
			};
		},
		created() {
			this.getBusinessClass();
		},
		computed: {
			headers() {
				let userinfoStr = window.localStorage.getItem('userinfo');
				if (userinfoStr) {
					let user = JSON.parse(userinfoStr);
					return {
						'Authorization': 'Bearer ' + user['msg']
					}
				}
			}
		},
		methods: {
			timeChage(val) {
				console.log(val);
				let d = new Date(val);
				this.birth = d.getFullYear() + '.' + (d.getMonth()+1) + '.' + d.getDate();
			},
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let userinfo = JSON.parse(window.localStorage.getItem('userinfo'));
						let id = userinfo['data']['id'];
						var createTime = new Date();
						var createAt = Number(createTime);
						if(this.imageUrl == "") {
							this.$message("");
							return false;
						}
						var params = {
							// "id": id,
							"status": this.applyForm.status,
							"financing": this.applyForm.financing,
							"archived": this.applyForm.archived,
							"unit": this.applyForm.unit,
							"name": this.applyForm.name,
							"logoId": this.logoId,
							"logo": this.logo,
							"intro": this.applyForm.intro,
							"scale": this.applyForm.scale,
							"businessId": this.applyForm.businessId,
							"what": this.applyForm.what,
							"how": this.applyForm.how,
							"why": this.applyForm.why,
							"founder": this.applyForm.founder,
							"founderIntro": this.applyForm.founderIntro,
							"progress": this.applyForm.progress,
							"birth": this.birth,
							"ratio": this.applyForm.ratio,
							"edu": this.applyForm.edu,
							"graduate": this.applyForm.graduate,
							"specialty": this.applyForm.specialty,
							"createAt": createAt
						}
						api.Post('/finance', params)
							.then(res => {
								if (res['suc'] == true) {
									this.$message('');
									window.history.go(-1);
								} else if (res['suc'] == false) {
									this.$message('');
								}
							});
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			getBusinessClass() { //
				api.Get("/dict/business").then(res => {
					this.category = res;
				});
			},
			beforeAvatarUpload(file) {
				const isJPG = file.type === 'image/*';
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isLt2M) {
					this.$message.error(' 2MB!');
				}
				return isLt2M;
			},
			handleAvatarSuccess(res, file) {
				this.logoId = res['data']['id'];
				this.logo = res['data']['uri'];
				this.imageUrl = res['data']['uri'];
			},
			handleRemove(file, fileList) {
				console.log(file, fileList);
			},
			handlePreview(file) {
				console.log(file);
			}
		}
	}
</script>




