<template>
  <div>
    <el-row v-if="!problemFlag" :gutter="10" style="background-color:rgb(238, 238, 238);padding-top: 50px;padding-bottom: 50px;">
      <el-col :lg="12" :md="12" :sm="12" :xs="12" :offset="6" style="background-color:#fff;padding:30px 25px 15px 0">
        <h1 class="tc f18" style="color:#0089e3;margin-bottom:30px;"></h1>
        <el-form :model="serviceForm" :rules="serviceRules" ref="serviceForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="classifyId">
            <el-select v-model="serviceForm.businessId" @change="change">
              <el-option :label="item['value']" :value="item['id']" v-for="(item, index) in category" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="title">
            <el-input v-model="serviceForm.title" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="needs">
            <el-input type="textarea" :rows="5" v-model="serviceForm.needs" placeholder="250"></el-input>
          </el-form-item>
          <el-form-item label="" prop="enterprise">
            <el-input v-model="serviceForm.enterprise" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="contact">
            <el-input v-model="serviceForm.contact" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="phone">
            <el-input v-model="serviceForm.phone" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button @click="publishForm('serviceForm')" style="background-color: #f48100;border:none;color:#fff;"></el-button>
            <el-button @click="resetForm('serviceForm')"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <el-row v-if="problemFlag" :gutter="10" style="background-color:rgb(238, 238, 238);padding-top: 50px;padding-bottom: 50px;">
      <el-col :lg="12" :md="12" :sm="12" :xs="12" :offset="6" style="background-color:#fff;padding:30px 25px 15px 0">
        <h1 class="tc f18" style="color:#0089e3;margin-bottom:30px;"></h1>
        <el-form :model="cosnultForm" :rules="cosnultRules" ref="cosnultForm" label-width="200px" class="demo-ruleForm">
          <el-form-item label="" prop="classifyId">
            <el-select v-model="cosnultForm.businessId">
              <el-option :label="item['value']" :value="item['id']" v-for="(item, index) in category" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="title">
            <el-input v-model="cosnultForm.title" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="content">
            <el-input type="textarea" :rows="5" v-model="cosnultForm.content" placeholder="250"></el-input>
          </el-form-item>
          <el-form-item label="" prop="enterprise">
            <el-input v-model="cosnultForm.enterprise" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="product">
            <el-input type="textarea" v-model="cosnultForm.product" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="contact">
            <el-input v-model="cosnultForm.contact" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="phone">
            <el-input v-model="cosnultForm.phone" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button @click="consultForm('cosnultForm')" style="background-color: #f48100;border:none;color:#fff;"></el-button>
            <el-button @click="resetForm('cosnultForm')"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import api from "../axios/api.js";
export default {
  data() {
    let validateContact = (rule, value, callback) => {
      let re = /^[\u4E00-\u9FA5\uf900-\ufa2d]{2,5}$/;
      if (
        value === "" ||
        !re.test(value) ||
        value.length < 2 ||
        value.length > 5
      ) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    //
    let validatePhone = (rule, value, callback) => {
      let re = /(^1[34578]\d{9}$)|(^0\d{2,3}-\d{7,8}$)|(^\d{7,8}$)/;
      if (value === "" || !re.test(value) || value.length < 7) {
        console.log(value);
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      category: "",
      problemFlag: false,
      serviceForm: {
        businessId: "", //
        title: "", //
        mentorId: "", //
        enterprise: "", //
        contact: "", //
        product: "", //
        phone: "", //
        needs: "", //
      },
      serviceRules: {
        businessId: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        title: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        mentorId: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        enterprise: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        contact: [
          {
            required: true,
            validator: validateContact,
            message: "",
            trigger: "blur"
          }
        ],
        needs: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          { min: 2, max: 250, message: '250', trigger: 'blur' }
        ],
        product: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        phone: [
          {
            required: true,
            validator: validatePhone,
            message: "",
            trigger: "blur"
          }
        ]
      },
      cosnultForm: {
        businessId: "", //
        title: "", //
        mentorId: "", //
        enterprise: "", //
        contact: "", //
        product: "", //
        phone: "", //
        content: ""
      },
      cosnultRules: {
        businessId: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        enterprise: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          { min: 1, max: 20, message: " 20 ", trigger: "blur" }
        ],
        contact: [
          {
            required: true,
            validator: validateContact,
            message: "",
            trigger: "blur"
          },
          { min: 1, max: 10, message: " 10 ", trigger: "blur" }
        ],
        phone: [
          {
            required: true,
            validator: validatePhone,
            message: "",
            trigger: "blur"
          }
        ],
        title: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          { min: 1, max: 40, message: " 40 ", trigger: "blur" }
        ],
        content: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          { min: 2, max: 250, message: '250', trigger: 'blur' }
        ],
        product: [
          {
            // required: true,
            message: "",
            trigger: "blur"
          }
        ]
      }
    };
  },
  created() {
    let type = this.$route.params.type;
    if (type == "enterprise") {
      // alert('');
      this.getServiceClass();
    } else if (type == "consult") {
      this.problemFlag = true;
      this.getConsultClass();
    }
  },
  methods: {
    publishForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          var params = {
            classifyId: this.serviceForm.businessId,
            enterprise: this.serviceForm.enterprise,
            contact: this.serviceForm.contact,
            phone: this.serviceForm.phone,
            title: this.serviceForm.title,
            needs: this.serviceForm.needs
          };
          api.Post("/enterprise/apply", params).then(res => {
            console.log(res);
            if (res["suc"] == true) {
              this.$message("");
              window.history.go(-1);
            } else {
              this.$message(res["msg"]);
            }
          });
        } else {
          console.log("!");
          return false;
        }
      });
    },
    consultForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          var params = {
            businessId: this.cosnultForm.businessId,
            enterprise: this.cosnultForm.enterprise,
            contact: this.cosnultForm.contact,
            phone: this.cosnultForm.phone,
            title: this.cosnultForm.title,
            content: this.cosnultForm.content,
            product: this.cosnultForm.product,
            mentorId: 0
          };
          api.Post("/consult", params).then(res => {
            console.log(res);
            if (res["suc"] == true) {
              this.$message("");
              window.history.go(-1);
            } else {
              this.$message(res["msg"]);
            }
          });
        } else {
          console.log("!");
          return false;
        }
      });
    },
    change: function() {
      console.log(this.cosnultForm.businessId);
    },
    getServiceClass() {
      //
      api.Get("/dict/service").then(res => {
        this.category = res;
      });
    },
    getConsultClass() {
      //
      api.Get("/dict/consult").then(res => {
        this.category = res;
      });
    }
  }
};
</script>



