<template>
	<div>
		<el-row :gutter="10" style="background-color:rgb(238, 238, 238);padding-top: 50px;padding-bottom: 50px;">
			<el-col :lg="10" :md="10" :sm="10" :xs="10" :offset="7" style="background-color:#fff;padding:30px 25px 15px 0">
				<el-form :model="serviceNeedsForm" :rules="serviceNeedsRules" ref="trainForm" label-width="100px" class="demo-ruleForm">
					<el-form-item label="" prop="classifyId">
						<el-select v-model="serviceNeedsForm.classifyId">
							<el-option label="" value="shanghai"></el-option>
							<el-option label="" value="beijing"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="" prop="title">
						<el-input v-model="serviceNeedsForm.title" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="" prop="needs">
						<el-input type="textarea" v-model="serviceNeedsForm.needs" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="" prop="enterprise">
						<el-input v-model="serviceNeedsForm.enterprise" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="" prop="contact">
						<el-input v-model="serviceNeedsForm.contact" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="" prop="phone">
						<el-input v-model="serviceNeedsForm.phone" placeholder=""></el-input>
					</el-form-item>
					<el-form-item>
						<el-button @click="sureForm('serviceNeedsForm')" style="background-color: #f48100;border:none;color:#fff;"></el-button>
						<el-button @click="resetForm('serviceNeedsForm')"></el-button>
					</el-form-item>
				</el-form>
			</el-col>
		</el-row>
	</div>
</template>


<script type="text/ecmascript-6">
	import api from '../axios/api.js'
	export default {
		data() {
			//
			//			let validateEnterprise = (rule, value, callback) => {
			//				let re = /^[\u4E00-\u9FA5\uf900-\ufa2d]{2,5}$/;
			//				if (value === "" || !re.test(value) || value.length < 2 || value.length > 5) {
			//					callback(new Error("!"));
			//				} else {
			//					callback();
			//				}
			//			};
			//
			let validateContact = (rule, value, callback) => {
				let re = /^[\u4E00-\u9FA5\uf900-\ufa2d]{2,5}$/;
				if (value === "" || !re.test(value) || value.length < 2 || value.length > 5) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			//
			let validatePhone = (rule, value, callback) => {
				let re = /(^1[34578]\d{9}$)|(^0\d{2,3}-\d{7,8}$)|(^\d{7,8}$)/;
				if (value === "" || !re.test(value) || value.length < 7) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			return {
				serviceNeedsForm: {
					classifyId: '', //
					title: '', //
					needs: '', //
					enterprise: '', //
					contact: '', //
					phone: '' //
				},
				serviceNeedsRules: {
					classifyId: [{
						required: true,
						message: '',
						trigger: 'change'
					}],
					title: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					needs: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					contact: [{
						required: true,
						validator: validateContact,
						message: '',
						trigger: 'blur'
					}],
					phone: [{
						required: true,
						validator: validatePhone,
						message: '',
						trigger: 'blur'
					}]
				}
			};
		},
		methods: {
			sureForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						var params = {
							//							"classifyId": this.serviceNeedsForm.classifyId,
							//							"title": this.serviceNeedsForm.title,
							//							"needs": this.serviceNeedsForm.needs,
							"captcha": this.serviceNeedsForm.captcha,
							//							"enterprise": this.serviceNeedsForm.enterprise,
							"phone": this.serviceNeedsForm.phone
						};
						api.Post('/enterprise/apply', params)
							.then(res => {
								console.log(res);
								if (res['msg'] == null) {
									alert("");
								}
							});
					} else {
						console.log('!');
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			}
		}
	}
</script>

<style>

</style>


