<template>
  <div>
    <el-row :gutter="0" style="background-color:rgb(238, 238, 238);padding-top: 50px;padding-bottom: 50px;">
      <el-col :lg="14" :md="10" :sm="10" :xs="10" :offset="5">
        <el-form :model="tenancyForm" :rules="tenancyRules" ref="tenancyForm" label-width="120px" class="tenancyForm">
          <el-form-item class="tc f18 b">{{typeFlag}}</el-form-item>
          <el-form-item label="" prop="title">
            <el-input type="text" v-model="tenancyForm.title"></el-input>
          </el-form-item>
          <el-form-item label="" prop="contact">
            <el-input type="text" v-model="tenancyForm.contact"></el-input>
          </el-form-item>
          <el-form-item label="" prop="phone">
            <el-input type="tel" v-model="tenancyForm.phone"></el-input>
          </el-form-item>
          <el-form-item label="" prop="district">
            <el-input type="text" v-model="tenancyForm.district"></el-input>
          </el-form-item>
          <el-form-item label="" prop="building">
            <el-input type="text" v-model="tenancyForm.building"></el-input>
          </el-form-item>
          <el-form-item label="" prop="buildingType">
            <el-select v-model="tenancyForm.buildingType">
              <el-option :label="item['label']" :value="item['value']" v-for="(item, index) in options" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" v-if="typeFlag == ''">
            <el-upload :headers="headers" :data="upLoadData" class="upload-demo" ref="upload" action="http://192.168.11.222/servant/file/batch" :on-success="handleAvatarSuccess" :on-remove="handleRemove" :file-list="fileList">
              <el-button slot="trigger" size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
            </el-upload>
          </el-form-item>
          <el-form-item label="" prop="area">
            <el-input type="number" v-model="tenancyForm.area"></el-input>
          </el-form-item>
          <el-form-item label="" prop="address">
            <el-input type="text" v-model="tenancyForm.address"></el-input>
          </el-form-item>
          <el-form-item label="" prop="area">
            <el-input type="number" v-model="tenancyForm.price"></el-input>
          </el-form-item>
          <el-form-item label="" prop="unit">
            <el-select v-model="tenancyForm.unit">
              <el-option :label="item['label']" :value="item['value']" v-for="(item, index) in units" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="cubicle">
            <el-radio-group v-model="tenancyForm.cubicle">
              <el-radio label="true"></el-radio>
              <el-radio label="false"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="content">
            <vue-editor v-model="tenancyForm.content"></vue-editor>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('tenancyForm')"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<style scoped>
  .info {
    border-radius: 10px;
    line-height: 20px;
    padding: 10px;
    margin: 10px;
    background-color: #ffffff;
  }
</style>

<script>
  import {
    VueEditor
  } from "vue2-editor";
  import api from "../axios/api.js";
  export default {
    components: {
      VueEditor
    },
    data() {
      let validatePhone = (rule, value, callback) => {
        let re = /(^1[34578]\d{9}$)|(^0\d{2,3}-\d{7,8}$)|(^\d{7,8}$)/;
        if (value === "" || !re.test(value) || value.length < 7) {
          console.log(value);
          callback(new Error(""));
        } else {
          callback();
        }
      };
      let validateName = (rule, value, callback) => {
        let re = /^[\u4E00-\u9FA5\uf900-\ufa2d]{2,5}$/;
        if (
          value === "" ||
          !re.test(value) ||
          value.length < 2 ||
          value.length > 5
        ) {
          callback(new Error(""));
        } else {
          callback();
        }
      };
      return {
        upLoadData: {
          guid: null
        },
        fileList: [],
        typeFlag: "",
        htmlForEditor: null,
        form: {},
        options: [{
            value: "",
            label: ""
          },
          {
            value: "",
            label: ""
          }
        ],
        units: [{
            value: "//",
            label: "//"
          },
          {
            value: "//",
            label: "//"
          },
          {
            value: "//",
            label: "//"
          },
          {
            value: "//",
            label: "//"
          },
          {
            value: "/",
            label: "/"
          }
        ],
        tenancyForm: {
          title: "",
          phone: "",
          building: "",
          buildingType: "",
          area: "",
          address: "",
          price: "",
          unit: "",
          cubicle: "",
          content: ""
        },
        tenancyRules: {
          title: [{
            required: true,
            message: "",
            trigger: "blur"
          },
          { min: 1, max: 80, message: ' 80 ', trigger: 'blur' }],
          contact: [{
            required: true,
            validator: validateName,
            message: "",
            trigger: "blur"
          },
          { min: 1, max: 10, message: ' 10 ', trigger: 'blur' }],
          phone: [{
            required: true,
            validator: validatePhone,
            message: "",
            trigger: "blur"
          }],
          building: [{
            required: true,
            message: "",
            trigger: "blur"
          },
          { min: 1, max: 30, message: ' 30 ', trigger: 'blur' }],
          district: [{
            // required: true,
            message: "",
            trigger: "blur"
          },
          { min: 1, max: 45, message: ' 45 ', trigger: 'blur' }],
          area: [{
            required: true,
            message: "",
            trigger: "blur"
          },
          { min: 1, max: 30, message: ' 30 ', trigger: 'blur' }],
          address: [{
            required: true,
            message: "/",
            trigger: "blur"
          },
          { min: 1, max: 100, message: ' 100 ', trigger: 'blur' }],
          price: [{
            required: true,
            message: "",
            trigger: "blur"
          },
          { min: 1, max: 30, message: ' 30 ', trigger: 'blur' }],
          unit: [{
            required: true,
            message: "",
            trigger: "blur"
          }],
          cubicle: [{
            // required: true,
            message: "",
            trigger: "blur"
          }],
          content: [{
            required: true,
            message: "",
            trigger: "blur"
          }]
        }
      };
    },
    created() {
      console.log(this.$route.params.type);
      if (this.$route.params.type == "1") {
        this.typeFlag = "";
      } else if (this.$route.params.type == "2") {
        this.typeFlag = "";
      }
    },
    computed: {
      headers() {
        let userinfoStr = window.localStorage.getItem("userinfo");
        if (userinfoStr) {
          let user = JSON.parse(userinfoStr);
          return {
            Authorization: "Bearer " + user["msg"]
          };
        }
      }
    },
    methods: {
      handleRemove(file, fileList) {
        let fileId = file["response"]["data"]["id"];
        let url = "/file/" + fileId;
        api.Delete(url, {
          id: fileId
        }).then(res => {});
      },
      handlePreview(fileList) {
        console.log(fileList);
      },
      handleAvatarSuccess(res, file) {
        console.log(res["data"]["guid"]);
        this.upLoadData["guid"] = res["data"]["guid"];
      },
      submitForm(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            let url = "/qb/tenancy/apply";
            var params = {
              categoryId: this.$route.params.categoryId,
              type: this.$route.params.type,
              contact: this.tenancyForm.contact,
              phone: this.tenancyForm.phone,
              district: this.tenancyForm.district,
              building: this.tenancyForm.building,
              buildingType: this.tenancyForm.buildingType,
              area: this.tenancyForm.area,
              address: this.tenancyForm.address,
              price: this.tenancyForm.price,
              unit: this.tenancyForm.unit,
              cubicle: this.tenancyForm.cubicle,
              detail: {
                title: this.tenancyForm.title,
                content: this.tenancyForm.content
              },
              photos: this.upLoadData["guid"]
            };
            api.Post("/qb/tenancy/apply", params).then(res => {
              console.log(res);
              if (res["suc"] == true) {
                this.$confirm("", "", { 
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "success"
                  })
                  .then(() => {
                    window.history.go(-1);
                  })
                  .catch(() => {
                    //
                  });
              } else {
                this.$message(res["msg"]);
              }
            });
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      watch: {
        $route(to, from) {
          if (this.$route.params.type == "1") {
            this.typeFlag = "";
          } else if (this.$route.params.type == "2") {
            this.typeFlag = "";
          }
        }
      }
    }
  };
</script>

<style>
  .tenancyForm {
    width: 600px;
    background-color: #fff;
    padding: 30px;
    margin-left: auto;
    margin-right: auto;
  }
  .tenancyForm .tc {
    color: #06affe;
    border-bottom: 2px solid #06affe;
  }
  .tenancyForm .tc .el-form-item__content {
    margin-left: 0 !important;
  }
  h2 {
    color: #20a0ff;
    font-size: 16px;
    font-weight: 600;
    margin: 20px 10px;
  }
  .line {
    text-align: center;
  }
</style>


